<template>
  <uploader
    :options="options"
    class="uploader-example"
    @file-success="onFileSuccess"
    @file-complete='fileComplete'
  >
    <uploader-unsupport></uploader-unsupport>
    <uploader-drop>
      <p>拖拽到这里上传或</p>
      <uploader-btn>选择文件</uploader-btn>
      <uploader-btn :attrs="attrs">选择图片</uploader-btn>
      <uploader-btn :directory="true">选择文件夹</uploader-btn>
    </uploader-drop>
    <uploader-list></uploader-list>
  </uploader>
</template>

<script>
var baseUrl = "";
export default {
  data() {
    return {
      options: {
        target: "", //SpringBoot后台接收文件夹数据的接口
        testChunks: false, //是否开启服务器分片校验
        chunkSize: "2048000", //分块大小2M(大文件自动分块上传)
        fileParameterName: "uploadFiles", //上传文件时文件的参数名，默认file
        query: { diagnoseId: "" } //包含在多部分POST中的额外参数和数据,默认值：{}
        //simultaneousUploads: 3 //同时上传数
      },
      attrs: {
        accept: "image/*"
      },
      statusText: {
        success: "成功了",
        error: "出错了",
        uploading: "上传中",
        paused: "暂停中",
        waiting: "等待中"
      }
    };
  },
  created() {
    debugger;
    var diagnoseId = this.$route.query.diagnoseId?this.$route.query.diagnoseId:""
    this.options.query = {diagnoseId: diagnoseId};
    this.options.target = this.baseUrl + "/Platform_N/pacs/technician/uploadPacsFilesBatch"; //SpringBoot后台接收文件夹数据的接口
    console.log("目前基本路径：" + this.baseUrl);
  },
  methods: {
    onFileSuccess: function(rootFile, file, response, chunk) {
      console.log(JSON.parse(response));
      //这里可以根据response（接口）返回的数据处理自己的实际问题（如：从response拿到后台返回的想要的数据进行组装并显示）
      //注，这里从文件夹每上传成功一个文件会调用一次这个方法
    },
    //根文件（文件夹）已完成
    fileComplete(rootFile) {
      console.log("上传完毕");
    }
  }
};
</script>


<style>
.uploader-example {
  width: 90%;
  padding: 15px;
  margin: 40px auto 0;
  font-size: 12px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
}

.uploader-example .uploader-btn {
  margin-right: 4px;
}

.uploader-example .uploader-list {
  max-height: 440px;
  overflow: auto;
  overflow-x: hidden;
  overflow-y: auto;
}
.uploader-drop {
  text-align: left;
}
.uploader-file-info {
  text-align: left;
}
</style>


